import React from 'react';
import "./ingame-game-select.css"
import EndzoneMultiSelect from '../../../../components/selectors/endzone-multiselect/endzone-multiselect'
import EndzoneSelect from '../../../../components/selectors/endzone-select/endzone-select';

class EndzoneInGameGameSelect extends React.Component {
    constructor (props) {
        super(props);
    }
    handleMultiSelectChange = (selections) => {
        // Flattened list of select object
        let selected_data_list = []
        selections.map((selection) => {
            selected_data_list.push(selection.value)
        })
        return selected_data_list
    }

    render() {
        return(
            <div className='game-selection-div'>
                <div className='game-selection-header'><h2>Select Games</h2></div>
                <div className='game-selection-body'>
                    <p>Filter Games by a Team of Interest (An Opponent or Yourself) or Year, then select a game below.</p>
                    <div className='select-search'>
                        <label className='game-select-labels'>Teams of Interest:</label>
                            <EndzoneSelect data = {this.props.team_options} handleChange = {(selection) => this.props.handleTeamChange(this.handleMultiSelectChange(selection))}>
                            </EndzoneSelect>
                        <label className='game-select-labels'>Search by Year:</label>
                            <EndzoneMultiSelect data = {this.props.year_options} handleChange = {(selection) => this.props.handleYearChange(this.handleMultiSelectChange(selection))}>
                            </EndzoneMultiSelect>
                        {this.props.report_type ? 
                            <div>
                                <label className='game-select-labels'>Report Type:</label>
                                <EndzoneSelect data = {this.props.report_type} handleChange = {(selection) => this.props.handleReportTypeChange(this.handleMultiSelectChange(selection))}>
                                </EndzoneSelect>
                            </div>
                        : ''}
                    </div>
                    <div className='select-game-div'>
                        <label className='game-select-labels'>Select Historic Games:</label>
                        <EndzoneMultiSelect data = {this.props.game_options} handleChange = {(selection) => this.props.handleHistoricGamesChange(this.handleMultiSelectChange(selection))}></EndzoneMultiSelect>
                        <label className='game-select-labels'>Select Current Game:</label>
                        <EndzoneSelect data = {this.props.game_options} handleChange = {(selection) => this.props.handleCurrentGameChange(this.handleMultiSelectChange(selection))}></EndzoneSelect>
                    </div>
                </div>
            </div>
        );
    }
}



export default EndzoneInGameGameSelect;