import React from 'react';
import "./in-game-report.css"
import EndzoneNav from '../../../components/navigators/endzone-nav/EndzoneNav';
import EndzoneButton from '../../../components/buttons/endzone-new-button/endzone-button' 
import PlayCircleOutlineIcon from '@mui/icons-material/PlayCircleOutline';
import EndzoneInGameGameSelect from './ingame-game-select/ingame-game-select';
import logo from './images/Endzone_Shield.png'
import axios from 'axios'
import Checkbox from '@mui/material/Checkbox';
import FormControlLabel from '@mui/material/FormControlLabel';
import { Typography , createTheme } from '@mui/material';
import EndzoneAlert from '../../../components/misc-items/endzone-alert/endzone-alert'

const theme = createTheme({
    typography: {
      fontFamily: 'Audiowide',
    }
})

class ToolPage extends React.Component {
    constructor (props) {
        super(props);
        this.state = {
            isLoading: false,
            isBaseScreenActivated: true,
            isConfigureActivated: false,
            viewUrl: false,
            documentUrl: '',
            pack_data: null,
            game_data: [],
            game_objects: [],
            year_options: [],
            team_options: [],
            game_options: [],
            selected_historic_games: [],
            selected_current_game: "",
            selected_years: [],
            selected_teams: []
            
        }
        this.selected_options = []
        this.alertRef = React.createRef()
    }

    componentDidMount = () => {
        this.getToolMetaData()
        this.getGames()
        
    }

    getToolMetaData = () => {
        axios.get('/endzone/reports/pregame/metadata').then((response) => {
          this.setState({pack_data: response.data})
        }, (error) => {
          //To-Do display error
          this.setState({pack_data: null})
        })

    }
    
    getGames = async () => {
        axios.get('/endzone/data/game/get').then((response) => {
            this.setState({game_data: response.data}, () => {
                this.loadGames(this.state.game_data)
            })
          }, (error) => {
            //To-Do display error
            this.setState({game_data: null})
          })
    }

    loadGames = (game_data) => {
        let teams_of_interest_setList = new Set()
        let years_setList = new Set()
        let game_object = []

        game_data.map((game) => {
            if(!teams_of_interest_setList.has(game.Away_Team)) {   
                teams_of_interest_setList.add(game.Away_Team)
            }
            if(!teams_of_interest_setList.has(game.Home_Team)) {
                teams_of_interest_setList.add(game.Home_Team)
            }
            let game_date = new Date(game.Game_Date)
            let year = game_date.getUTCFullYear()
            if(!years_setList.has(year)){
                years_setList.add(year)
            }

            let game_string = game.Home_Team + " vs " + game.Away_Team + " - " + game_date.toDateString()
            console.log(game)
            game_object.push({gameLabel: game_string, Home_Team: game.Home_Team, Away_Team: game.Away_Team, Year: game_date.getUTCFullYear(), Game_ID: game.Game_ID})

        }) 
        this.setState({
            year_options: this.convertSingularListToSelectDict([...years_setList]),
            team_options: this.convertSingularListToSelectDict([...teams_of_interest_setList]),
            game_objects: game_object
        }, () => {
            this.filterGames()
        })
    }

    filterGames = () => {
        let game_objects = this.state.game_objects;
        let filtered_objects = [];
        let selected_years = this.state.selected_years;
        let selected_teams = this.state.selected_teams;

        
        game_objects.map((game_object) => {
            if((selected_teams.length == 0  || selected_teams.includes(game_object.Away_Team) || selected_teams.includes(game_object.Home_Team)) && (selected_years.length == 0  || selected_years.includes(game_object.Year))) {
                filtered_objects.push({value: game_object.Game_ID, label: game_object.gameLabel})
            }
            
        })
        this.setState({game_options: filtered_objects})
    }

    convertSingularListToSelectDict = (list) => {
        let updated_list = []
        list.map((elm) => {
            updated_list.push({value: elm, label: elm})
        }) 
        return updated_list
    }

    runTool = async () => {
        if(this.state.selected_teams.length == 0) {
            this.alertRef.current.openAlert("A team of Interest is required to generate a report.", "warning");
            return;
        }
        else if(this.state.selected_historic_games.length == 0) {
            this.alertRef.current.openAlert("At least one historic game is required to create a report.", "warning");
            return;
        }
        else if(this.state.selected_current_game.length == 0){
            this.alertRef.current.openAlert("Please select a current game.", "warning");
            return;
        }
        this.alertRef.current.closeAlert();

        this.runtimeLoading();
        await this.requestReport();
        this.runtimeComplete();
    }

    activateConfigure = () => {
        this.setState({
            isConfigureActivated: !this.state.isConfigureActivated,
            isBaseScreenActivated: !this.state.isBaseScreenActivated,
            isLoading: false
        })
    }

    requestReport = async () => {
        await axios.get(
            "/endzone/reports/ingame/run", {
                params: {
                    requestedPages: this.selected_options.toString(),
                    requestedTeamOfInterest: this.state.selected_teams.toString(),
                    requestedPriorGames: this.state.selected_historic_games.toString(),
                    requestedGame: this.state.selected_current_game
                },
                responseType: 'blob'
            }
        ).then (
            (response) => {
                console.log("SUCCESS happened here")
                const blobUrl = URL.createObjectURL(new Blob([response.data], { type: 'application/pdf' }));
                this.setState({documentUrl: blobUrl, viewUrl: true})
                window.open(blobUrl, '_blank');
            },
            (error) => {
                console.log("ERROR happened here")
                this.alertRef.current.handleRequestError(error)
            }
    )}

    runtimeLoading =  () => {
        this.setState({
            isBaseScreenActivated: true,
            isLoading: true,
            isConfigureActivated: false,
        })
    }

    runtimeComplete = () => {
        this.setState({
            isBaseScreenActivated: true,
            isLoading: false,
            isConfigureActivated: false
        })
    }


    render() {
        return(
            <div className='tool-page'>
                <EndzoneNav links = {[{title: "Return to In-Game", link: "/endzone/ingame/hub"}]}></EndzoneNav>
                <div className='tool-input-div'>
                    <EndzoneAlert ref={this.alertRef}></EndzoneAlert>
                    <div className='tool-header'>
                        <h3>In-Game Report</h3>
                    </div>
                    <div className='tool-body'>
                        <div className='tool-games'>
                            <EndzoneInGameGameSelect 
                                year_options = {this.state.year_options} 
                                team_options = {this.state.team_options} 
                                game_options = {this.state.game_options}
                                handleTeamChange = {(selectedList) => {this.setState({selected_teams: selectedList}, () => this.filterGames())}}
                                handleYearChange = {(selectedList) => {this.setState({selected_years: selectedList}, () => this.filterGames())}}
                                handleHistoricGamesChange = {(selectedList) => {this.setState({selected_historic_games: selectedList})}}
                                handleCurrentGameChange = {(selectedList) => {this.setState({selected_current_game: selectedList[0]})}}
                            ></EndzoneInGameGameSelect>
                        </div>
                        <div className='tool-container' style={{zIndex: 10000}}>
                            <EndzoneButton onClick = {(e) => {this.runTool()}} style={{"backgroundColor": "#054f88"}} text = "Run Tool" icon = {<PlayCircleOutlineIcon sx = {{"font-size": '150%'}}></PlayCircleOutlineIcon>}></EndzoneButton>
                        </div>
                    </div>
                </div>
                <div className='tool-output-div'>
                    <div className='load-screen' id = "load-screen" style={{display: this.state.isBaseScreenActivated ? 'inline-flex' : 'none'}}>
                        <div className='load-div' id = "load-div" style={{animation: this.state.isLoading ? "loading_spin 1.25s linear infinite" : ''}}>
                            <img src={logo} className = "tool-logo"></img>
                            <h3 style={{opacity: this.state.isLoading ? 1 : 0}} className ='loading-text'>Loading...</h3>
                            <a href={this.state.documentUrl} style={{display: this.state.viewUrl ? 'block' : 'none'}}>Click to View Report</a>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}



export default ToolPage;